@import './../../styles/colors.scss';
@import './../../styles/global.scss';
@import './../../styles/mixin.scss';

.experience {
	@include small-section;

	.job {
		@include flex-content(flex, column);
		border-left: 1px solid $color-blue-dark;
		padding-left: 20px;
		margin: 25px 0px;
	
		&__date {
			background-color: $color-blue-light;
			width: max-content;
			padding: 5px 10px;
		}
	
		&__info {
			@include flex-content(flex, column, center);
			margin-top: 10px;
		}
	
		&__title {
			@include font;
		}
	
		&__company {
			@include font(18px, lighter);
			margin: 5px 0;
		}
	
		&__description {
			@include font(16px, normal);
			padding-left: 20px;
	
			li {
				list-style-position: outside;
				list-style-type: circle;
			}
		}
		
	}

	@media screen and (min-width: 1024px) {
		@include section;

		.job {
			@include flex-content;
			border-left: none;
			padding-left: 0;
			margin: 0;

			&__date {
				background-color: transparent;
				width: 20%;
				padding-right: 20px;
				padding-top: 5px;
				@include flex-content(flex, row, flex-end, flex-start);
			}

			&__info {
				@include flex-content(flex, column, center);
				margin-top: 0;
				width: 70%;
				border-left: 1px solid $color-blue-dark;
				padding-left: 20px;
				padding-bottom: 50px;
			}
		}
	}
}